*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: var(--font-family, sans-serif);
  background-color: var(--bg-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

html {
  scroll-behavior: smooth;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link-reset {
  font-size: 18px;
  text-decoration: none;
  color: var(--black-color);
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.hidden {
  display: none;
}

.hidden-img {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}
@media (width <= 1152px) {
  .container {
    padding: 0 24px;
  }
}
@media (width <= 788px) {
  .container {
    padding: 0 16px;
  }
}

body.blurred .wrapper {
  filter: blur(10px);
  pointer-events: none;
}

.header {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
}
.header__nav {
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.header__link {
  border-radius: 8px;
  padding: 10.5px 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--button-bg-color);
}
.header__link:focus {
  outline: none;
}
.header__link-logo {
  border-radius: 0;
  padding: 0;
  font-family: var(--second-family);
  font-weight: 900;
  font-size: 32px;
  color: var(--subaccent-color);
  background-color: transparent;
}
.header__item:not(:last-child) {
  margin-right: 20px;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 450px;
}
.header__btn {
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .header__btn:hover {
    color: var(--bg-color);
    background-color: var(--text-color);
  }
}
.header__btn:focus {
  outline: none;
  color: var(--bg-color);
  background-color: var(--text-color);
}
.header__btn-theme {
  display: flex;
  align-items: center;
  border-radius: 8px;
  padding: 9px 10px;
  background-color: var(--button-bg-color);
}
@media (any-hover: hover) {
  .header__btn-theme:hover .header__icon {
    fill: var(--bg-color);
    transition: all 0.3s ease-in-out;
  }
}
.header__btn-theme:focus {
  background-color: var(--text-color);
}
.header__btn-theme:focus .header__icon {
  fill: var(--bg-color);
}
.header__btn-contacts {
  border-radius: 8px;
  padding: 11px 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--button-bg-color);
}
.header__icon {
  width: 20px;
  height: 20px;
  fill: var(--text-color);
}
.header__search {
  outline: none;
  border: 1px solid var(--button-bg-color);
  border-radius: 8px;
  padding: 10px 15px;
  width: 100%;
  max-width: 242px;
  font-size: 14px;
  color: var(--text-color);
  background-color: var(--button-bg-color);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .header__search:hover {
    border: 1px solid var(--text-color);
  }
}
.header__search:focus {
  border: 1px solid var(--text-color);
}

.hero {
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  color: var(--text-color);
}
.hero__descr {
  max-width: 30%;
}
.hero__subtitle {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
}
.hero__text {
  margin: 0;
  margin-bottom: 20px;
  font-size: 14px;
  color: var(--subtext-color);
}
.hero__count {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  padding: 15px 30px;
  background-color: var(--button-bg-color);
}
.hero__count:not(:last-child) {
  margin-bottom: 20px;
}
.hero__number {
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 24px;
  color: var(--accent-color);
}
.hero__name {
  position: relative;
  margin-bottom: 10px;
  outline: none;
  font-weight: 900;
  font-size: 24px;
  color: var(--accent-color);
  transition: all 0.3s ease-in-out;
}
.hero__total {
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  color: var(--subtext-color);
  transition: all 0.3s ease-in-out;
}
.hero__last {
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  color: var(--subtext-color);
}
.hero__slider {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  width: 67%;
  font-family: var(--second-family);
  font-weight: 900;
  font-size: 72px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--accent-color);
}
.hero__icon {
  position: absolute;
  top: -5px;
  width: 14px;
  height: 14px;
  fill: #FF782F;
}
.hero__btn {
  outline: none;
  border: 1px solid var(--button-bg-color);
  border-radius: 8px;
  padding: 15px 30px;
  width: 100%;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--button-bg-color);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .hero__btn:hover {
    border: 1px solid var(--subaccent-color);
  }
  .hero__btn:hover .hero__name {
    color: var(--subaccent-color);
  }
}
.hero__btn:focus {
  border: 1px solid var(--subaccent-color);
}
.hero__btn:focus .hero__name {
  color: var(--subaccent-color);
}

.project {
  flex-direction: column;
  padding-bottom: 60px;
}
.project__title {
  margin: 0 auto;
  margin-bottom: 20px;
  padding-top: 20px;
  font-weight: 900;
  font-size: 36px;
  text-transform: uppercase;
  color: var(--text-color);
}
.project__filter {
  margin-bottom: 30px;
}
.project__list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.project__btn {
  outline: none;
  border-radius: 8px;
  padding: 11px 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--button-bg-color);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .project__btn:hover {
    color: var(--bg-color);
    background-color: var(--text-color);
  }
}
.project__btn:focus {
  color: var(--bg-color);
  background-color: var(--text-color);
}
.project__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.project__card-list {
  flex-wrap: wrap;
  margin-bottom: 30px;
  gap: 30px;
}
.project__card-item {
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  padding: 20px;
  width: 300px;
  color: var(--text-color);
}
.project__card-logo {
  margin-bottom: 10px;
  border: 2px solid var(--accent-color);
  border-radius: 100%;
  width: 60px;
  height: 60px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.project__card-name {
  margin-bottom: 10px;
  font-weight: 900;
  text-transform: uppercase;
}
.project__card-category {
  margin-bottom: 10px;
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  padding: 4px 6px 3px;
  max-width: fit-content;
  font-size: 10px;
  text-transform: uppercase;
}
.project__card-descr {
  margin-bottom: 20px;
  border-bottom: 1px solid var(--accent-color);
  min-height: 110px;
  font-size: 12px;
  line-height: 2;
}
.project__card-btn {
  outline: none;
  border-radius: 8px;
  padding: 20px 40px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-color);
  background-color: var(--button-bg-color);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .project__card-btn:hover {
    color: var(--bg-color);
    background-color: var(--text-color);
  }
}
.project__card-btn:focus {
  color: var(--bg-color);
  background-color: var(--text-color);
}
.project__social-icon {
  width: 24px;
  height: 24px;
  fill: var(--text-color);
}
.project__social-item {
  width: 24px;
  height: 24px;
}
.project__social-item:not(:last-child) {
  margin-right: 10px;
}
.project__social-link {
  outline: none;
}
@media (any-hover: hover) {
  .project__social-link:hover .project__social-icon {
    fill: var(--subaccent-color);
    transition: all 0.3s ease-in-out;
  }
}
.project__social-link:focus .project__social-icon {
  fill: var(--subaccent-color);
  transition: all 0.3s ease-in-out;
}

.footer {
  position: relative;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
}
.footer::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: calc(100% - 60px);
  height: 1px;
  background-color: var(--accent-color);
  transform: translateX(-50%);
}
.footer__link {
  border-radius: 0;
  padding: 0;
  font-family: var(--second-family);
  font-weight: 900;
  font-size: 16px;
  color: var(--subaccent-color);
  background-color: transparent;
}
.footer__text {
  margin: 0;
  font-size: 12px;
  color: var(--subtext-color);
}
.footer__btn {
  outline: none;
  border-radius: 8px;
  padding: 11px 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--bg-color);
  background-color: var(--subaccent-color);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .footer__btn:hover {
    color: var(--bg-color);
    background-color: var(--accent-color);
  }
}
.footer__btn:focus {
  color: var(--bg-color);
  background-color: var(--accent-color);
}

.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: -1;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  padding: 30px;
  width: 50%;
  background-color: var(--bg-color);
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.modal.active {
  z-index: 1000;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.modal__text {
  margin: 0;
  margin-bottom: 20px;
  font-size: 24px;
  color: var(--text-color);
}
.modal__text-sm {
  margin-bottom: 60px;
  font-size: 16px;
  color: var(--subtext-color);
}
.modal__list {
  justify-content: space-between;
  margin-bottom: 30px;
  width: 100%;
}
.modal__item {
  border: 1px solid var(--subaccent-color);
  border-radius: 8px;
  padding: 10px 10px 6px;
}
.modal__icon {
  width: 20px;
  height: 20px;
  fill: var(--subaccent-color);
}
.modal__icon-close {
  width: 20px;
  height: 20px;
  stroke: var(--text-color);
}
.modal__btn {
  border-radius: 8px;
  padding: 11px 30px;
  max-width: fit-content;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--bg-color);
  background-color: var(--accent-color);
}
.modal__btn-close {
  position: absolute;
  right: 30px;
  top: 30px;
  padding: 0;
  background-color: transparent;
}