.project {
  flex-direction: column;
  padding-bottom: 60px;

  &__title {
    margin: 0 auto;
    margin-bottom: 20px;
    padding-top: 20px;
    font-weight: 900;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--text-color);
  }

  &__filter {
    margin-bottom: 30px;
  }

  &__list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  &__btn {
    outline: none;
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--text-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--text-color);
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__card-list {
    flex-wrap: wrap;
    margin-bottom: 30px;
    gap: 30px;
  }

  &__card-item {
    border: 1px solid var(--accent-color);
    border-radius: 8px;
    padding: 20px;
    width: 300px;
    color: var(--text-color);
  }

  &__card-logo {
    margin-bottom: 10px;
    border: 2px solid var(--accent-color);
    border-radius: 100%;
    width: 60px;
    height: 60px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__card-name {
    margin-bottom: 10px;
    font-weight: 900;
    text-transform: uppercase;
  }

  &__card-category {
    margin-bottom: 10px;
    border: 1px solid var(--accent-color);
    border-radius: 8px;
    padding: 4px 6px 3px;
    max-width: fit-content;
    font-size: 10px;
    text-transform: uppercase;
  }

  &__card-descr {
    margin-bottom: 20px;
    border-bottom: 1px solid var(--accent-color);
    min-height: 110px;
    font-size: 12px;
    line-height: 2;
  }

  &__card-btn {
    outline: none;
    border-radius: 8px;
    padding: 20px 40px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--text-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--text-color);
    }
  }

  &__social-icon {
    width: 24px;
    height: 24px;
    fill: var(--text-color);
  }

  &__social-item {
    width: 24px;
    height: 24px;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__social-link {
    outline: none;

    @include hover {
      .project__social-icon {
        fill: var(--subaccent-color);
        transition: all .3s ease-in-out;
      }
    }

    &:focus {
      .project__social-icon {
        fill: var(--subaccent-color);
        transition: all .3s ease-in-out;
      }
    }
  }
}
