.hero {
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  color: var(--text-color);

  &__descr {
    max-width: 30%;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-size: 36px;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 14px;
    color: var(--subtext-color);
  }

  &__count {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 15px 30px;
    background-color: var(--button-bg-color);

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__number {
    margin-bottom: 10px;
    font-weight: 900;
    font-size: 24px;
    color: var(--accent-color);
  }

  &__name {
    position: relative;
    margin-bottom: 10px;
    outline: none;
    font-weight: 900;
    font-size: 24px;
    color: var(--accent-color);
    transition: all .3s ease-in-out;
  }

  &__total {
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--subtext-color);
    transition: all .3s ease-in-out;
  }

  &__last {
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--subtext-color);
  }

  &__slider {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    width: 67%;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 72px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--accent-color);
  }

  &__icon {
    position: absolute;
    top: -5px;
    width: 14px;
    height: 14px;
    fill: #FF782F;
  }

  &__btn {
    outline: none;
    border: 1px solid var(--button-bg-color);
    border-radius: 8px;
    padding: 15px 30px;
    width: 100%;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      border: 1px solid var(--subaccent-color);

      .hero__name {
        color: var(--subaccent-color);
      }
    }

    &:focus {
      border: 1px solid var(--subaccent-color);

      .hero__name {
        color: var(--subaccent-color);
      }
    }
  }
}
