.footer {
  position: relative;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    width: calc(100% - 60px);
    height: 1px;
    background-color: var(--accent-color);
    transform: translateX(-50%);
  }

  &__link {
    border-radius: 0;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 16px;
    color: var(--subaccent-color);
    background-color: transparent;
  }

  &__text {
    margin: 0;
    font-size: 12px;
    color: var(--subtext-color);
  }

  &__btn {
    outline: none;
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--bg-color);
    background-color: var(--subaccent-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--accent-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--accent-color);
    }
  }
}
