.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: -1;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  padding: 30px;
  width: 50%;
  background-color: var(--bg-color);
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%);
  pointer-events: none;

  &.active {
    z-index: 1000;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    color: var(--text-color);

    &-sm {
      margin-bottom: 60px;
      font-size: 16px;
      color: var(--subtext-color);
    }
  }

  &__list {
    justify-content: space-between;
    margin-bottom: 30px;
    width: 100%;
  }

  &__item {
    border: 1px solid var(--subaccent-color);
    border-radius: 8px;
    padding: 10px 10px 6px;
  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: var(--subaccent-color);

    &-close {
      width: 20px;
      height: 20px;
      stroke: var(--text-color);
    }
  }

  &__btn {
    border-radius: 8px;
    padding: 11px 30px;
    max-width: fit-content;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--bg-color);
    background-color: var(--accent-color);

    &-close {
      position: absolute;
      right: 30px;
      top: 30px;
      padding: 0;
      background-color: transparent;
    }
  }
}
