.header {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;

  &__nav {
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__link {
    border-radius: 8px;
    padding: 10.5px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);

    &:focus {
      outline: none;
    }
  }

  &__link-logo {
    border-radius: 0;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 32px;
    color: var(--subaccent-color);
    background-color: transparent;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 450px;
  }

  &__btn {
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--text-color);
    }

    &:focus {
      outline: none;
      color: var(--bg-color);
      background-color: var(--text-color);
    }
  }

  &__btn-theme {
    display: flex;
    align-items: center;
    border-radius: 8px;
    padding: 9px 10px;
    background-color: var(--button-bg-color);

    @include hover {
      .header__icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }

    &:focus {
      background-color: var(--text-color);

      .header__icon {
        fill: var(--bg-color); // или нужный цвет
      }
    }
  }

  &__btn-contacts {
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: var(--text-color);
  }

  &__search {
    outline: none;
    border: 1px solid var(--button-bg-color);
    border-radius: 8px;
    padding: 10px 15px;
    width: 100%;
    max-width: 242px;
    font-size: 14px;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      border: 1px solid var(--text-color);
    }

    &:focus {
      border: 1px solid var(--text-color);
    }
  }
}
